<template>
	<div class="Replenishment">
		<header><i class="iconfont icon-xiangzuo1" @click="goPrev"></i><span>补货</span><i class="iconfont icon-saoma" @click="scan"></i></header>
		<div class="container">
			<div>
				<div>
					<span>自库位</span>
					<input  readonly  v-model="From_LibraryPosition"></input>
				</div>
				<div class="scanBtn">
					<span    @click="from_scanClick">扫</span>
					<input class="normalStyle"  v-model="From_scan"  @focus="g_focus('From_scan')" ref="From_scan"  @keyup.enter="getFocus('ConfirmNum')" ></input>
					<i v-if="From_scan" @click="clearInput('From_scan')"  class="iconfont icon-cuowu"></i>
				</div>
			</div>
			<div>
				<span>商品代码</span>
				<input   readonly  v-model="input_CommodityCode"></input>
			</div>

			<div class="goodsName">
				<span>商品名称</span>
				<textarea v-model="input_CommodityName"   readonly></textarea>
			</div>
			<div class="goodsName property">
				<span>批次属性</span>
				<textarea  readonly>
货物状态:{{lottable02}}
生产批号:{{lottable03}}
生产日期:{{lottable04}}
到期日期:{{Lottable05}}
				</textarea>
			</div>
			<div>
				<div>
					<span>补货数量</span>
					<input  readonly   v-model="replenish_Num"></input>
				</div>
				<div>
					<span>可用数量</span>
					<input  readonly   v-model="AvailableQuantity"></input>
				</div>
			</div>
			<div>
				<div>
					<span>确认数量</span>
					<input  type="number"  ref="ConfirmNum" class="normalStyle"   @change="toUppercase('ConfirmNum')"    @focus="g_focus('ConfirmNum')"  @keyup.enter="getFocus('To_scan')"    v-model="ConfirmNum"></input>
					<i v-if="ConfirmNum" @click="clearInput('ConfirmNum')"  class="iconfont icon-cuowu"></i>
				</div>
				<div>
					<span>单位</span>
					<input  readonly  v-model="input_Company"></input>
				</div>
			</div>
			<div>
				<div>
					<span>目的库位</span>
					<input  readonly  v-model="To_LibraryPosition"></input>
				</div>
				<div class="scanBtn">
					<span    @click="to_scanClick">扫</span>
					<input  ref="To_scan"  class="normalStyle" v-model="To_scan"   @focus="g_focus('To_scan')"  @keyup.enter="submit"   ></input>
					<i v-if="To_scan" @click="clearInput('To_scan')"  class="iconfont icon-cuowu"></i>
				</div>
			</div>
			<span class="g_singleBtn" @click="submit">确认</span>
		</div>
		<div v-if="tipsHide" @touchmove.prevent class="g_tips">
			<div>
				<span>提示信息</span>
				<span>{{msg}}</span>
				<p  @click='tipsErrorHide(inputFocus,ifRouter)'>确定</p>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"Replenishment",
		data(){
			return{
				tipsHide:false,//提示框是否出现
				msg:"",//提示信息
				inputFocus:"",
				From_LibraryPosition:'',//自库位
				From_scan:"",//自库位扫描信息
				input_CommodityCode:'',//商品代码
				input_CommodityName:'',//商品名称
				lottable02:'',//货物状态
				lottable03:'',//生产批号
				lottable04:'',//生产日期
				Lottable05:'',//到期日期
				replenish_Num:"",//补货数量
				AvailableQuantity:'',//可用数量
				ConfirmNum:'',//确认数量
				input_Company:'',//单位
				To_LibraryPosition:'',//目的库位
				To_scan:'',//目的库位扫描信息
				Replenish_TaskData:[],//任务数据 （总数据）
				ReplenishIndex:0,//当前是第几条任务数据
				ReplenishData:{},//当前显示数据
				ifRouter:false,//是否补货完成
			}
		},
		mounted() {

			this.$refs.From_scan.focus();
			window.QRScanAjax = this.QRScanAjax;
			$("input").attr("maxlength","50");
		},
		created(){

			var that = this;

			if(this.$store.state.Replenish_TaskData.length){

				this.Replenish_TaskData = this.$store.state.Replenish_TaskData;

				var ReplenishIndex = this.$route.query.ReplenishIndex;

				this.ReplenishIndex = ReplenishIndex;

				var ReplenishData = this.$store.state.Replenish_TaskData[ReplenishIndex];

				this.ReplenishData = ReplenishData;

				that.replaceTemplate();

				console.log(ReplenishData)

			}
		},
		methods:{
			// 返回上一页
			goPrev:function(){

				var that = this;

				this.$router.push({
					name:"ReplenishTaskList",
					query:{
						ifChangeStatus:true
					}
				})
			},
			// 接收iOS/android值
			QRScanAjax:function (qrcode){

				var inputName = this.$store.state.ScanQRCodeInputName;
				this[inputName] = qrcode;
				
				// 扫  自库位
				if(inputName == "From_scan") {

					this.getFocus('ConfirmNum');
					
				// 扫  确认数量
				}if(inputName == "ConfirmNum") {

					this.getFocus('To_scan');
					
				// 扫  目的库位
				}else if(inputName == "To_scan") {

					this.getFocus('submit');

				}
			},
			// 确认补货
			submit:function(){

				var that = this;

				if(!this.From_scan){
					
					that.msg = "请确认自库位";

					that.inputFocus = "From_scan";

					that.tipsShow("To_scan");

					return;
				}else if(!this.To_scan){

					that.msg = "请确认目的库位";

					that.inputFocus = "To_scan";

					that.tipsShow("To_scan");

					return;

				}else if(!this.ConfirmNum){

					that.msg = "请输入补货数量";

					that.inputFocus = "ConfirmNum";

					that.tipsShow("To_scan");

					return;

				}else if(this.From_scan != this.From_LibraryPosition){

					that.msg = "自库位不一致";

					that.inputFocus = "From_scan";

					that.tipsShow("To_scan");

					return;

				}else if(this.ConfirmNum < 0 || this.ConfirmNum > this.AvailableQuantity){

					that.msg = "确认数量必须大于零且少于可用数量";

					that.inputFocus = "ConfirmNum";

					that.tipsShow("To_scan");

					return;

				}else if(this.ConfirmNum != parseInt(this.ConfirmNum)){

					that.msg = "请输入正确的数量";

					that.inputFocus = "ConfirmNum";

					that.tipsShow("To_scan");

					return;
				}else if(this.To_scan != this.To_LibraryPosition){

					that.msg = "目的库位不一致";

					that.inputFocus = "To_scan";

					that.tipsShow("To_scan");

					return;
				}

				var AbolishTasksUrl = this.HOST + "RFRepConfirm.do";

				var AbolishTasksData = {
					TaskDetailKey:that.Replenish_TaskData[that.ReplenishIndex].TaskDetailKey,
					QtyConfirm:that.ConfirmNum,
				}

				var Replenish_StatusArr = that.$store.state.Replenish_StatusArr;

				this.httpRequest(AbolishTasksUrl,AbolishTasksData,function(res){

					// 改变状态
					Replenish_StatusArr[that.ReplenishIndex] = 9;

					that.$store.state.Replenish_StatusArr = Replenish_StatusArr;

					if(that.ReplenishIndex == that.Replenish_TaskData.length-1){

						that.msg = "补货已完成";

						that.tipsShow("To_scan");

						that.ifRouter = true;//补货完成，返回上一页

					}else{

						//任务号 +1
						that.ReplenishIndex++;

						var ifReplenishLast = false;

						for(var m = that.ReplenishIndex;m < Replenish_StatusArr.length ; m++){

							if(Replenish_StatusArr[m] == 0){

								that.ReplenishData = that.Replenish_TaskData[m];//当前页面数据更新

								that.replaceTemplate();//当前页面数据替换

								return;
							}else{
								ifReplenishLast = true;
							}
						}

						if(ifReplenishLast){
							that.msg = "补货已完成";

							that.tipsShow("To_scan");

							that.ifRouter = true;//补货完成，返回上一页
						}


					}

				},"From_scan")
			},
			// 
			from_scanClick:function(){

				this.From_scan = this.From_LibraryPosition;
			},
			to_scanClick:function(){

				this.To_scan = this.To_LibraryPosition;
			},
			// 替换当前页面显示数据模板
			replaceTemplate:function(){

				this.From_LibraryPosition = this.ReplenishData.FromLoc;//自库位
				this.From_scan = "";//自库位扫描信息
				this.input_CommodityCode = this.ReplenishData.Sku;//商品代码
				this.input_CommodityName = this.ReplenishData.Descr;//商品名称
				this.lottable02 = this.ReplenishData.Lottable02;//货物状态
				this.lottable03 = this.ReplenishData.Lottable03;//生产批号
				this.lottable04 = this.ReplenishData.Lottable04;//生产日期
				this.Lottable05 = this.ReplenishData.Lottable05;//到期日期
				this.replenish_Num = this.ReplenishData.Qty;//补货数量
				this.AvailableQuantity = this.ReplenishData.QtyAvailable;//可用数量
				this.ConfirmNum = this.ReplenishData.Qty;//确认数量
				this.input_Company = this.ReplenishData.Susr2;//单位
				this.To_LibraryPosition = this.ReplenishData.ToLoc;//目的库位
				this.To_scan = "";//目的库位扫描信息
			},
			//错误提示框消失
			tipsErrorHide:function (input,ifRouter){

				this.tipsHide = false ;

				if(ifRouter){
					// 补货完成  返回上一页
					this.$router.push({
						name:"ReplenishTaskList",
						query:{
							ifChangeStatus:true
						}
					})
				}

				if(input){

					this.$refs[input].select();
				}
			}
		}
	}
</script>
<style lang="less" scoped>
	.Replenishment{
		.container{
			padding-top: 20/50rem;
			input{
				font-size: 30/50rem;
				background-color: #f0f0f0;
			}
			&>div{
				width:100%;
				height: 70/50rem;
				overflow: hidden;
				span{
					width: 130/50rem;
					margin-right: 10/50rem;
					float: left;
					text-align: right;
				}
				.scanBtn{
					span{
						width: 80/50rem;
						height: 50/50rem;
						line-height: 50/50rem;
						margin-left: 50/50rem;
						text-align: center;
						border-radius: 5/50rem;
						background-color: #f0f0f0;
					}
				}
				&>input{
					width:575/50rem;
				}
				input{
					float: left;
					height:50/50rem;
					line-height: 50/50rem;
					padding-left: 5/50rem;
					box-sizing: border-box;
					border:none;
					outline: none;
				}
				input.normalStyle{
					background-color: #fff;
					border:1/50rem solid #bbb;
				}
				input.normalStyle:focus{
					outline:#7BD039 solid 3/50rem; 
				}
				&>div{
					width:375/50rem;
					float: left;
					position: relative;
					input{
						width:200/50rem;
					}
					.icon-cuowu{
						position:absolute;
						right:46/50rem;
						font-size: 28/50rem;
						top: 8/50rem;
						color: #bbb;
					}
				}
			}
			&>.goodsName{
				height:80/50rem;
				margin-bottom: 15/50rem;
				overflow: hidden;
				&>span{
					width: 130/50rem;
					text-align: right;
					float: left;
				}
				&>textarea{
					width:573/50rem;
					height:80/50rem;
					float:left;
					outline:none;
					font-size: 29/50rem;
					background-color: #f0f0f0;
					border:none;
				}
			}
			&>.property{
				height:140/50rem;
				&>textarea{
					height:140/50rem;
				}
			}
		}
	}

	@media screen and (max-width: 374px){
		.Replenishment{
			.container{
				&>div{
					input.normalStyle{
						border:3/50rem solid #bbb;
					}
				}
			}
		}
	}
	@media screen and (max-width: 330px){
		.Replenishment{
			.container{
				padding-top: 5/50rem;
				font-size: 38/50rem;
				input{
					font-size: 40/50rem;
				}
				&>div{
					height:65/50rem;
					span{
						width: 160/50rem;
						margin-right: 0;
					}
					.scanBtn{
						span{
							height: 60/50rem;
							line-height: 60/50rem;
							margin-left: 80/50rem;
						}
					}
					input{
						height:60/50rem;
						line-height: 60/50rem;
					}
					&>div{
						width:390/50rem;
						.icon-cuowu{
							right:15/50rem;
							font-size: 40/50rem;
							top: 10/50rem;
						}
						input{
							width:218/50rem;
						}
					}
					&>div:nth-child(1){
						width:360/50rem;
						input{
							width:200/50rem;
						}
					}
				}
				&>.goodsName{
					margin-bottom: 5/50rem;
					&>span{
						width: 160/50rem;
					}
					&>textarea{
						width:576.5/50rem;
						font-size: 32/50rem;
					}
				}
				&>.property{
					height:150/50rem;
					&>textarea{
						height:150/50rem;
					}
				}
			}
		}
	}
	@media screen and (max-width: 250px){
		.Replenishment{
			.container{
				font-size: 45/50rem;
				padding-top: 15/50rem;
				input{
					font-size: 45/50rem;
				}
				&>div{
					height:85/50rem;
					span{
						width: 180/50rem;
						margin-right: 0;
					}
					.scanBtn{
						span{
							height: 70/50rem;
							line-height: 70/50rem;
							margin-left: 10/50rem;
						}
						input{
							width: 270/50rem;
						}
						.icon-cuowu{
							right:25/50rem;
						}
					}
					&>input{
						width:560/50rem;
					}
					input{
						height:70/50rem;
						line-height: 70/50rem;
					}
					&>div{
						width:370/50rem;
						.icon-cuowu{
							right:15/50rem;
							font-size: 43/50rem;
							top: 14/50rem;
						}
						input{
							width:180/50rem;
						}
					}
					&>div:nth-child(1){
						width:380/50rem;
						input{
							width:200/50rem;
						}
					}
				}
				&>.goodsName{
					margin-bottom: 15/50rem;
					&>span{
						width: 180/50rem;
					}
					&>textarea{
						width:560/50rem;
						font-size: 40/50rem;
					}
				}
				&>.property{
					height:185/50rem;
					&>textarea{
						height:185/50rem;
					}
				}
			
			}
		}
	}
</style>


